.weapon-view {
  padding: 1rem; }
  @media (max-width: 419px) {
    .weapon-view {
      width: 100%;
      padding: 0.4rem; } }
  .weapon-view .name {
    padding-bottom: 1rem;
    border-bottom: 1px solid #dee2e6;
    color: #228be6; }
  .weapon-view .item-info {
    display: flex;
    align-items: center;
    flex-wrap: wrap; }
    .weapon-view .item-info .img {
      width: 95px;
      height: 95px;
      background-color: white;
      border: 1px solid #dee2e6;
      border-radius: 0.4em;
      padding: 2px;
      margin-right: 1rem; }
      .weapon-view .item-info .img img {
        width: 90px;
        height: 90px; }
    .weapon-view .item-info .option {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      font-weight: bold;
      height: 80px;
      min-width: 100px; }
      .weapon-view .item-info .option .dmg {
        color: #c92a2a; }
      .weapon-view .item-info .option .cri {
        color: #fcc419; }
      .weapon-view .item-info .option .body_nm0 {
        color: #212529; }
      .weapon-view .item-info .option .body_nm1 {
        color: #40c057; }
      .weapon-view .item-info .option .body_nm2 {
        color: goldenrod; }
      .weapon-view .item-info .option .body_nm3 {
        color: #e64980; }
      .weapon-view .item-info .option .body_nm4 {
        color: gold; }
      .weapon-view .item-info .option .body_nm5 {
        color: #339af0; }
      .weapon-view .item-info .option .body_nm6 {
        color: #fa5252; }
    .weapon-view .item-info .sub_option {
      min-width: 300px;
      margin-left: auto;
      display: flex;
      justify-content: space-between;
      padding-top: 0.5rem; }
      .weapon-view .item-info .sub_option .tier {
        width: 60px;
        text-align: center; }
      .weapon-view .item-info .sub_option .detail_dv {
        width: 60px;
        text-align: center; }
      .weapon-view .item-info .sub_option .size {
        width: 60px;
        text-align: center; }
      .weapon-view .item-info .sub_option .speed {
        width: 60px;
        text-align: center; }
      .weapon-view .item-info .sub_option .accuracy_rate {
        width: 60px;
        text-align: center; }
      .weapon-view .item-info .sub_option .point_rate {
        width: 60px;
        text-align: center; }
      .weapon-view .item-info .sub_option .illegal {
        width: 60px;
        text-align: center; }
      .weapon-view .item-info .sub_option div + div {
        border-left: 1px solid #dee2e6; }
