.list-wrapper {
  border: 1px solid #e9ecef;
  width: 1024px;
  margin: 0 auto;
  margin-bottom: 0.5rem;
  background: white; }
  @media (max-width: 1399px) {
    .list-wrapper {
      width: 768px; } }
  @media (max-width: 1023px) {
    .list-wrapper {
      width: 512px; } }
  @media (max-width: 767px) {
    .list-wrapper {
      width: 100%; } }
